body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.navbar{
  background: black;
  color: rgba(255, 255, 255, 0.829);
  font-size: 14px;
  font-weight: 500;
  padding: 14px;
}

.leftmenu ul{
  list-style-type: none;
  margin: 5px;
}
.leftmenu ul li{
  font-family: sans-serif;
  display: inline;
  margin: 0 20px 0 0;
}
.leftmenu ul li a{
  color: rgba(255, 255, 255, 0.829);
  text-decoration: none;
}

.rightmenu ul{
  list-style-type: none;
  margin: 5px;
}
.rightmenu ul li{
  font-family: sans-serif;
  display: inline-block;
  margin: 0 20px 0 0;
  text-transform: uppercase;
}
.rightmenu ul li button:hover{
  cursor: pointer;
}
.rightmenu ul li:hover{
  cursor: pointer;
}

.card-title, .card-text{
  color: rgb(255, 115, 0);
}
.card-title{
    font-weight: 500;
    letter-spacing: 6px;
}

.card{
    width: 18rem;
    display: grid;
    grid-template-rows: 180px 185px;
    margin: 5px 0px 0px 0px;
    border: none;
    padding: 5px;
    letter-spacing: 2px;
    margin-bottom: 30px;
    overflow: hidden;
}
.card:hover{
  transition: 0.60s ease;
  font-size: 24px;
  font-family: 'Times New Roman', Times, serif;
}

.back {
  background: linear-gradient(to right, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.5)), url(https://d2kektcjb0ajja.cloudfront.net/images/posts/feature_images/000/000/072/large-1466557422-feature.jpg?1466557422);
  height: 450px;
  background-size: cover;
}
.text h1{
  margin-top: 190px;
  margin-left: 200px;
    position: absolute;
  z-index: auto;
  color: aliceblue;
  font-family:  'Brush Script MT', cursive;
  font-size: 64px;
}
.text h2{
  font-family:  'Brush Script MT', cursive;
  margin-top: 265px;
  margin-left: 220px;
    position: absolute;
  color: rgb(190, 190, 190);
}
.frntbtn{
  margin-left: 215px;
  margin-top: 325px;
  letter-spacing: 8px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  padding: 12px;
  font-size: 18px;
  font-weight: bold;
}
@media (max-width: 592px){
  .text h1{
    margin-left: 20px;
  }
  .text h2{
    margin-left: 20px;
  }
  .frntbtn{
    margin-left: 20px;
  }
}